.App {
  text-align: center;
  background-color: #ffffff;
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  font-size: calc(10px + 2vmin);
  color: black;
}
.topBar{
  display: flex;
  flex-direction: row;
  min-width: 98%;
  margin-top: 15px;
  justify-content: flex-end;
}
.content{
  margin-top:10px ;
}
.bar{
  margin-top: 40px;
  margin-bottom: 30px;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}


.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
